				//this.inputEco
				// 判断ECO单次兑换最大额度
				let limit = await this.$contract.maxExEcoOnce();
				limit = this.$util.hexToDec(limit._hex);
				console.log("ECO单次兑换最大额度:", limit);
				if (number * 1 > limit) {
					console.log('共振ECO数量超出了最大值');
					alert(this.$t('Tips10'));
					this.sendLoading = false;
					return;
				}
				console.log('usdt兑换eoc的推荐人和值', referrer, value);
				let res = await this.$contract.vending(referrer, value);
				// 刷新数据
				this.init();
				this.sendLoading = false;
				console.log('usdt兑换eoc的返回值', res);
			},

			/* 第二块-ECO持有量 */
			// --- 已弃用
			async getE2() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let res = await this.$contract.getEcoBalance(address);
				let res2 = await this.$contract.calcLMineIncome(address);
				let res3 = await this.$contract.withdrawalAmount(address);
				let e = {
					e1: this.$util.hexTo10To18number3(res._hex),
					e2: this.$util.hexTo10To18number3(res2._hex),
					e3: this.$util.hexTo10To18number3(res3._hex),
				}
				this.e2 = e;
			},
			// 点击了可提取数量 - 提取
			async e21() {
				if (this.e2Loding) {
					console.log('不能重复点击');
					return;
				};
				this.e2Loding = true;
				// 判断波长币够不够执行手续费
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let balance1 = await this.$contract.getTRXBalance(address);
				balance1 = this.$util.div10e6(balance1);
				if(balance1 < this.$contract.gTronTips) {
					alert(this.$t('Tips11'));
				}
				//let res = await this.$contract.takeUnlockIncome();
				let res = await this.$contract.takeLMineIncome();
				this.e2Loding = false;
				await this.getE2();
				console.log('点击了可提取数量 - 提现', res);
			},

			/* 第三款-ESD激活 */
			// 这一块主要处理邀请链接和推荐人地址
			async getE3() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				// 显示自己的地址
				this.e3.e2 = this.$util.addressCurtail(address);
				// 自己完整的地址+url
				this.e3.e22 = window.location.origin + '/#/?referrer=' + address;
				// 获取我的邀请人地址
				let res2 = await this.$contract.userReferrerTickets(address);
				// 获取ESD激活对应的数量
				let res5 = await this.$contract.totalVendingTickets();
				res5 = this.$util.hexTo10To18number3(res5._hex);
				if (res5 < 10000000) {
					this.e3.e1 = 1000;
				} else if (res5 < 15000000) {
					this.e3.e1 = 500;
				} else if (res5 < 21000000) {
					this.e3.e1 = 400;
				} else if (res5 < 25000000) {
					this.e3.e1 = 200;
				} else {
					this.e3.e1 = 100;
				};
				console.log('E3返回值:', res2);
				// 缩写推荐人地址
				let referrer = '';
				// 完整推荐人地址
				let referrer2 = '';
				if (res2.referrer === '410000000000000000000000000000000000000000') {
					// 说明没有合约里玩过
					// 再判断url里面有没有推荐人referrer
					if (this.$route.query.referrer) {
						// 说明url有推荐人
						referrer = this.$util.addressCurtail(this.$route.query.referrer);
						referrer2 = this.$route.query.referrer;
					} else {
						// 说明url没有推荐人
						referrer = '-';
						referrer2 = '';
					}
				} else {
					// 玩过, 就肯定有推荐人
					referrer = this.$util.addressCurtail(await this.$contract.addressFromHex(res2.referrer));
					referrer2 = await this.$contract.addressFromHex(res2.referrer);
				}
				this.e3.e3 = referrer;
				this.e3.e33 = referrer2;
				// 判断有木有激活ESD, 投资1000的那个
				let res9 = await this.$contract.isActiveEid(address);
				this.isActive = res9.active;
			},
			// 点击了ESD激活 - 激活
			async e31() {
				// 判断有木有激活过
				if (this.isActive) {
					console.log('已经激活了ESD');
					return;
				};
				// 不能重复点击
				if (this.e3Loading1) {
					console.log('不能重复点击');
					return;
				}
				this.e3Loading1 = true;
				// 判断ECO余额是否足够
				let address = await this.$contract.defaultAddress();
				address = address.base58;

        // 判断波长币够不够执行手续费
        let balance1 = await this.$contract.getTRXBalance(address);
        balance1 = this.$util.div10e6(balance1);
        if(balance1 < this.$contract.gTronTips) {
            alert(this.$t('Tips11'));
        }

				let ecoBalance = await this.$contract.getEcoBalance(address);
				ecoBalance = this.$util.hexTo10To18number3(ecoBalance._hex);
				if (ecoBalance < 1000) {
					// 考虑到金额四舍五入的准度问题
					this.e3Loading1 = false;
					alert(this.$t('Tips3'));
					return;
				};
				// 获取地址
				let url1;
				if(this.$route.query.referrer) {
					url1 = this.$route.query.referrer;
				}else {
					url1 = this.$contract.defaultReferrer;
				}
				let res = await this.$contract.activeEid(url1);
				this.e3Loading1 = false;
				await this.getE3();
				// 刷新持有量
				await this.getE2();
				console.log('点击了ESD激活 - 激活', res)
        setTimeout(async () => {
              // 刷新数据
              this.init();
         }, 1000);
			},
			// 点击了复制ESD邀请链接 - 复制
			async e32() {
				console.log(' 点击了复制ESD邀请链接 - 复制');
				let value = this.e3.e22;
				let input = document.createElement("input");
				input.value = value;
				document.body.appendChild(input);
				input.select();
				input.setSelectionRange(0, input.value.length), document.execCommand('Copy');
				document.body.removeChild(input);
				// 复制弹框
				alert('Copy succeeded')
			},
			// 点击了ESD共振收益 - 提现
			async e33() {
				// 不能重复点击
				if (this.e3Loading2) {
					console.log('不能重复点击');
					return;
				}
				this.e3Loading2 = true;

        // 判断波长币够不够执行手续费
        let address = await this.$contract.defaultAddress();
        address = address.base58;
        let balance1 = await this.$contract.getTRXBalance(address);
        balance1 = this.$util.div10e6(balance1);
        if(balance1 < this.$contract.gTronTips) {
            alert(this.$t('Tips11'));
        }

				let res = await this.$contract.takeEidExIncome();
				this.e3Loading2 = false;
				await this.getAll();
				console.log('点击了ESD共振收益 - 提现', res);
			},

			/* 第四块-预计可提现收益 */
			async getE4() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let res = await this.$contract.expectedIncome(address);
				let res2 = await this.$contract.eidMatIncome(address);
				let res3 = await this.$contract.eidNodeIncome(address);
				this.e4.e1 = this.$util.hexTo10To18number3(res._hex);
				this.e4.e2 = this.$util.hexTo10To18number3(res2._hex);
				this.e4.e3 = this.$util.hexTo10To18number3(res3._hex);
			},
			// 点击了最大的提现按钮
			async e41() {
				if (this.e4Loading) {
					console.log('不能重复点击');
					return;
				};
				this.e4Loading = true;
        // 判断波长币够不够执行手续费
        let address = await this.$contract.defaultAddress();
        address = address.base58;
        let balance1 = await this.$contract.getTRXBalance(address);
        balance1 = this.$util.div10e6(balance1);
        if(balance1 < this.$contract.gTronTips) {
            alert(this.$t('Tips11'));
        }
				let res = await this.$contract.take();
				this.e4Loading = false;
          // 刷新数据
          await this.init();
				console.log('点击了提现按钮', res)
          setTimeout(async () => {
              // 刷新数据
              await this.init();
          }, 2000);
        setTimeout(async () => {
          // 刷新数据
          await this.init();
        }, 5000);
        setTimeout(async () => {
          // 刷新数据
          await this.init();
        }, 10000);
			},

			/* 第五块-E级节点 */
			async getE5() {
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				// 获取节点E1...
				let res = await this.$contract.eidNodes(address);
				this.e5.e1 = res.nodeLevel;
			},
			// 选中质押的数量
			selectAmount(amount) {
				if (amount === 1) {
					this.e5.e6 = 1;
					this.e5.e2 = 3000;
				} else if (amount === 2) {
					this.e5.e6 = 2;
					this.e5.e2 = 5000;
				} else if (amount === 3) {
					this.e5.e6 = 3;
					this.e5.e2 = 10000;
				} else if (amount === 4) {
					this.e5.e6 = 4;
					this.e5.e2 = 15000;
				} else if (amount === 5) {
					this.e5.e6 = 5;
					this.e5.e2 = 20000;
				} else if (amount === 6) {
					this.e5.e6 = 6;
					this.e5.e2 = 25000;
				} else {
					this.e5.e6 = 0;
				}
				console.log('n', amount)
			},
			// 点击了质押
			async e51() {
				if (this.e5Loading1) {
					console.log('不能重复点击');
					return;
				};
				this.e5Loading1 = true;
				// 获取输入框的数据
				let number = this.e5.e2 * 1;
				// 判断ECO余额是否足够
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let ecoBalance = await this.$contract.getEcoBalance(address);
				ecoBalance = this.$util.hexTo10To18number3(ecoBalance._hex);
				if (ecoBalance < number) {
					// 考虑到金额四舍五入的准度问题
					this.e5Loading1 = false;
					alert(this.$t('Tips3'))
					return;
				};
				// 判断波长币够不够执行手续费
				let balance1 = await this.$contract.getTRXBalance(address);
				balance1 = this.$util.div10e6(balance1);
				if(balance1 < this.$contract.gTronTips) {
					alert(this.$t('Tips11'));
				}
				number = this.$util.mul10e18(number);
				let res = await this.$contract.pledgeNode(number);
				this.e5Loading1 = false;
				console.log('质押传入数据', number, res);
				//延迟2秒获取数据，开始倒计时
				setTimeout(async () => {
					this.getAll();
					//res = await this.$contract.getDataSet(address);
				}, 2000);

			},
			// 点击撤资
			async e52() {
				if (this.e5Loading2) {
					console.log('不能重复点击');
					return;
				}
				this.e5Loading2 = true;
				// 判断波长币够不够执行手续费
				let address = await this.$contract.defaultAddress();
				address = address.base58;
				let balance1 = await this.$contract.getTRXBalance(address);
				balance1 = this.$util.div10e6(balance1);
				if(balance1 < this.$contract.gTronTips) {
					alert(this.$t('Tips11'));
				}
				let res = await this.$contract.unPledgeNode();
				this.e5Loading2 = false;
				await this.getAll();
				console.log('点击了撤资', res)
			},

		}

	}
</script>

<style>
	/* 输入框 */
	/* 宽度尺度大于1500时 */
	@media only screen and (min-width: 415px) {
		.logo {
  			width: 300px;
  			height: 66px;
		}
		.v5-layout {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
			width: 100%;
		}

		.v5-2 {
			width: 100%;
			display: flex;
			justify-content: space-between;
		}

		.v5-input {
			padding: 5px 10px;
			font-size: 15px;
			border: 1px solid #F5F5F5;
			border-radius: 5px;
			height: 25px;
			margin-bottom: 10px;
			width: 330px;
		}
		.jinput-layout {
			padding: 30px 0 0 0;
			width: 70%;
			margin: 0 auto;
			display: flex;
			flex-direction: column;
			text-align: left;
		}

		.jinput-layout>div {
			margin-bottom: 10px;
		}

		/* 输入框 */
		.j-input-layout {
			display: flex;
		}

		.j-2 {
			display: flex;
		}

		.j-input:focus {
			outline: none;
		}

		.j-input {
			padding: 10px 20px;
			font-size: 20px;
			border-radius: 20px 0 0 20px;
			border: 1px solid #F5F5F5;
			height: 30px;
			width: 175px;
		}

		.j-usdt {
			background: #FFFFFF;
			line-height: 50px;
			text-align: center;
			padding: 0 20px;
			background: rgb(250, 250, 250);
			border-radius: 0 20px 20px 0;
		}

		.j-approve {
			background: #34CECC;
			color: #FFFFFF;
			text-align: center;
			margin-left: 20px;
			border-radius: 10px;
			line-height: 50px;
			min-width: 80px;
		}

		.j-img {
			margin: 0 20px;
			width: 150px;
			height: 25px;
			margin-top: 10px;
		}
	}

	/* 宽度尺寸小于414时 */
	@media only screen and (max-width: 414px) {
		.v5-layout {
			display: flex;
			flex-direction: column;
			align-items: flex-start;
		}

		.v5-2 {
			width: 100%;
		}

		.v5-input {
			padding: 5px 5px;
			font-size: 15px;
			border: 1px solid #F5F5F5;
			border-radius: 5px;
			height: 25px;
			margin-bottom: 10px;
			text-align: left;

		}

		.v5-2>div:nth-child(2) {
			margin-top: 20px;
			text-align: right;
		}

		.logo{
			width: 60%;
			height: 60%;
			margin-top: 20px;
		}
		.jinput-layout {
			padding: 30px 0 0 0;
			display: flex;
			flex-direction: column;
			text-align: left;
		}

		.jinput-layout>div {
			margin-bottom: 10px;
		}

		/* 输入框 */
		.j-input-layout {
			display: flex;
			flex-direction: column;
		}

		.j-2 {
			display: flex;
		}

		.j-input:focus {
			outline: none;
		}

		.j-input {
			padding: 10px 20px;
			font-size: 20px;
			border-radius: 20px 0 0 20px;
			border: 1px solid #F5F5F5;
			height: 30px;
			width: 60%;
		}

		.j-usdt {
			background: #FFFFFF;
			line-height: 50px;
			text-align: center;
			padding: 0 20px;
			background: rgb(250, 250, 250);
			border-radius: 0 20px 20px 0;
			width: 20%;
		}

		.j-approve {
			background: #34CECC;
			color: #FFFFFF;
			text-align: center;
			border-radius: 10px;
			line-height: 40px;
			min-width: 80px;
			margin-top: 10px;
		}

		.j-img {
			padding: 10px 0;
			width: 20px;
			height: 20px;
			margin: 0 auto;
		}

	}

	/* 折线图和剩下全部 */
	#main {
		height: 300px;
	}

	.all-w2 {
		background: #FFFFFF;
		border: 1px solid #F5F5F5;
		border-radius: 15px;
		margin-bottom: 30px;
		padding-left:15px;
	}

	/* 超出可以拉动的 - E1, E2, ... E5*/
	.v5-1 {
		display: flex;
		width: 100%;
		overflow-y: auto;
	}

	.v5 {
		background: #EDEEF2;
		color: #938AA2;
		padding: 3px 15px;
		margin-right: 20px;
		margin-bottom: 10px;
	}

	.v52 {
		background: #FFF000;
		color: #938AA2;
		padding: 3px 15px;
		margin-right: 20px;
		margin-bottom: 10px;
	}

	/* 超出可以拉动的- 1000U 5000U ... 25000U */
